.footer-social {
    background: $colorSecondary;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 34px;

    ul li {
        display: inline-block;
        margin-left: 8px;
    }
}

.main-footer {
    min-width: 80px;
    height: 165px;
    background: $colorPrimary;
    display: flex;
    color: $colorTertiary;

    [class*="col-"] {
        width:25%;
        margin: 30px 30px 30px 0;

        &:last-of-type {
            margin-right: 0;
        }
    }

    .footer-logo {
        display: flex;
        justify-content: flex-end;
    }

    .footer-contact ul {
        display: flex;
        flex-direction: column;
        align-items: center;

            li {
            list-style-type: none;
            margin-bottom: 18px;
            margin-left: 40px;
            width: 60%;

            a {
                color: $colorTertiary;
                text-decoration: none;
                position: relative;
                line-height: 20px;
                transition: 0.5s color;

                &:hover {
                    color: $colorSecondary;
                }

                .map-maker, .telephone {
                    &:before {
                        content: "";
                        position: absolute;
                        width: 24px;
                        height: 24px;
                        top: -6px;
                        left: -35px;
                    }
                }

                .map-maker {
                    &:before {
                        background-image: url("../images/google_maps_blue.png");
                    }
                }

                .telephone {
                    &:before {
                        background-image: url("../images/call _blue.png");
                    }
                }
            }
        }
    }

    .footer-nav li {
        list-style-type: none;
        margin-bottom: 18px;

        a {
            color: $colorTertiary;
            text-decoration: none;
            transition: 0.5s color;

            &:hover {
                color: $colorSecondary;
            }

            .greater-than-sign {
                color: $colorSecondary;
                font-weight: bold;
            }
        }
    }
}

//RWD
@media screen and (max-width:700px) {
    .short-description {display: none;}
    .main-footer [class*="col-"] {
        width: 25%;

        &:nth-of-type(3) {
			margin-right: 0;
            width: 50%;
		}
    }
    .main-footer .footer-logo {
        display: block;
        margin-left: 20px;
    }
}
@media screen and (max-width:455px) {
    .short-description, .footer-nav {display: none;}
    .main-footer [class*="col-"] {width: 50%;}
}

@media screen and (max-width:335px) {
    .main-footer .footer-logo {display: none;}
    .main-footer [class*="col-"] { &:nth-of-type(3){width: 100%;}}
}
